* {
  box-sizing:border-box;
}

body,html {
  min-height:100%;
  width:100%;
  margin:0;
}

body {
  @include typeDefault;
}

.App {
  width:100%;
  height:100vh;
  display:flex;
  flex-direction:column;
  justify-content: space-between;
  align-items: center;
}

%container {
  width:100%;
  max-width:em(1060);
  margin-left:auto;
  margin-right:auto;
  position:relative;
}

.app-main {
  @extend %container;
  &,svg,.component-wells {
    height:em(600);
  
  }
}

.helper {
  position:absolute;
  top:50%;
  left:em(220);
  max-width:300px;
  svg {
    display: inline-block;
    height: 1em;
    position:relative;
    top:.1em;
    margin-right: 0.5em;
    fill: $dark;
  }
  small,strong {
    display:block;
  }
  small {
    text-transform:uppercase;
    font-weight:bold;
    margin-bottom:1em;
    letter-spacing:1px;
  }
  strong {
    color:$blueDark;
    font-size:40px;
    line-height:1.2em;
  }
  &.helper-secondary {
    text-align:right;
    left:auto;
    right:em(220);
  }
}
