.component-tooltip {
  background:$light;
  border-radius:8px;
  box-shadow:5px 5px 25px #00000029;
  position:absolute;
  z-index:2;
  pointer-events:none;
  &.left-below,&.left-above {
    margin-right:20px;
  }
  &.right-below,&.right-above {
    margin-left:20px;
  }
  &.left-below,&.right-below {
    margin-top:20px;
  }
  &.left-above,&.right-above {
    margin-bottom:20px;
  }
  &.left-above {
    transform:translate(-100%,-100%);
  }
  &.right-above {
    transform:translateY(-100%);
  }
  &.left-below {
    transform:translateX(-100%);
  }
  &.tooltip-link {
    .header-primary,.header-secondary {
      border:none;
    }
  }
  .tooltip__wrapper {
    padding:1em;
  }
  .header-primary,.header-secondary {
    white-space:nowrap;
    //border-bottom:.5px solid $dark;
    display:inline-block;
  }
  .secondary {
    margin-top:2em;
  }
  ul.breakdown {
    list-style:none;
    padding:0;
    li {
      padding:0;
      margin:0 0 .5em;
    }
  }
}