.component-sankey {
  position:relative;
  z-index:1;
  &.drop-hover {
    z-index:-1;
  }
  rect {
    stroke:$light;
    stroke-width:2px;
    stroke-dasharray:200px,calc(200% + 200px);
  }
  &:hover {
    path,rect {
      transition:all .5s ease-out;
      opacity:.2;
      &.highlight {
        opacity:1;
      }
    }
  }
}