.component-wells {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  pointer-events:none;
  z-index:1;
  &.drop-hover {
    z-index:2;
  }
  .wells__wrapper,.wells__wells {
    height:100%;
  }
  .wells__wrapper {
    width:100%;
  }
  .wells__wells {
    display:flex;
    justify-content:space-between;
  }
  .wells__well {
    height:100%;
    width:em(200);
    border:2px dashed $grayLight;
    pointer-events:all;
    transition:all .2s ease-out;
    svg {
      color:$grayLight;
      height:2em;
      width:2em;
      position:absolute;
      left:-1em;
      top:calc(50% - .5em);
      line-height:1em;
      z-index:2;
      background:#fff;
      pointer-events:none;
    }
    &.wells__well-available {
      ~ .wells__well {
        opacity:.2;
        pointer-events:none;
        &.wells__well-available {
          opacity:1;
          pointer-events:all;
        }
      }
    }
    &.wells__well-active {
      border-color:transparent;
      z-index:-1;
      position:relative;
      svg {
        opacity:0;
      }
    }
    &.wells__well-hover {
      border-color:darken($grayLight,10%);
    }
    &:last-child {
      position:absolute;
      left:calc(1060px + (100vw - 1060px)/2 - 53px);
    }
  }
  .has_active-3 & {
    .wells__well {
      &:last-child {
        position:relative;
        left:0;
      }
    }
  }
}

.component-well-headers {
  @extend %container;
  .well__headers {
    display:flex;
    justify-content:space-between;
    align-items: center;
    width:100%;
  }
  .well__header {
    width:em(200);
    text-align:center;
    position:relative;
    &.well__header-active {
      &:before,&:after {
        content:"";
        border-color:$light;
        border-style:solid;
        position:absolute;
        bottom:.8em;
        width:em(43);
        height:em(24);
      }
      &:before {
        border-width:1px 0 0 1px;
        left:0;
      }
      &:after {
        border-width:1px 1px 0 0;
        right:0;
      }
    }
    &:last-child {
      position:absolute;
      left:calc(1060px + (100vw - 1060px)/2 - 100px);
    }
    h2 {
      @include typeHeaderWell;
      max-width: em(140,20);
      margin: 0 auto;
      text-align: center;
    }
  }
  .has_active-3 & {
    .well__header {
      &:last-child {
        position:relative;
        left:0;
      }
    }
  }
}